import io from 'socket.io-client'
import { useEffect, useState } from 'react';

import { Novedades } from '../../types';

import '../../index.css'

import { getNovPerPrioridad } from '../../services/novedades';
import ItemsNovedades from './itemsNovedades';
import {
  Tabs,
  TabsHeader,
  Tab,
  TabsBody,
  TabPanel
} from "@material-tailwind/react";

interface headersCount {
  [key: string]: any,
  Importante: number | undefined,
  Rutinario?: number | undefined,
  Historial?: number | undefined
}

function NovedadesTabs() {

  const headerNames = ['Importante', 'Rutinario', 'Historial']
  const [prioridad1, setPrioridad1] = useState<Novedades[] | undefined>(undefined)
  const [prioridad2, setPrioridad2] = useState<Novedades[] | undefined>(undefined)

  //cuenta de los elementos de cada prioridad
  const headersCount : headersCount= {
    Importante: prioridad1?.length,
    Rutinario: prioridad2?.length,
    Historial: 0
  }

  // se conecta al websocket
  const socket = io(
    'http://192.168.121.124:3001/',
    { transports : ['websocket'] }
  )

  //agrega la nueva novedad al estado pasado por parametro
  const addNovedad = (
    stateHandler: React.Dispatch<React.SetStateAction<Novedades[] | undefined>>, 
    add: Novedades    
    )=>{
    stateHandler((prevState: Novedades[]| undefined) =>
    [
      //devuelve los valores contenidos por el array
      ...(prevState?.map(prevState => prevState) ?? []), 
      add
    ]
    ) 
  }

  useEffect(()=> {
    getNovPerPrioridad(1)
    .then(json => {
      json.data?.map((item: Novedades) =>
        addNovedad(setPrioridad1, item)
      ) 
    })

    getNovPerPrioridad(2)
    .then(json => {
      json.data?.map((item: Novedades) =>
        addNovedad(setPrioridad2, item)
      ) 
    })

    socket.on('novedades', (data : Novedades )=>{ 
      if(data.prioridad === 1){
        addNovedad(setPrioridad1, data)   
      }
      else if( data.prioridad === 2 ){
        addNovedad(setPrioridad2, data)   
      }
    })
  }, 
  []) // eslint-disable-line

  return (
    <div className="App">
      <div className='sm:w-full md:w-1/2'>
        <Tabs value = {headerNames[0]}>
          <TabsHeader>
          {headerNames.map((headerName: string) =>(
              <Tab value={headerName} key={headerName } >
                {headerName} [{headersCount[headerName] === undefined ? 0 : headersCount[headerName] }]
              </Tab>
          ))}
          </TabsHeader>
          <div className='bg-gray-200 h-screen overflow-y-scroll overscroll-y-contain'>
          	<TabsBody 
          	  animate={{
          	    mount: { y: 0 },
          	    unmount: { y: 250 },
          	  }}>
          	  <TabPanel key='1' value={headerNames[0]}>
          	  	<ItemsNovedades  
          	  		dataObject={prioridad1} 
          	  		updateDataObject={setPrioridad1}
          	    />
          	  </TabPanel>
          	  <TabPanel key='2' value={headerNames[1]}>
          	  	<ItemsNovedades  
						      dataObject={prioridad2} 
						      updateDataObject={setPrioridad2} 
					      />
          	  </TabPanel>
          	  <TabPanel key='3' value={headerNames[2]}>
          	      <p>Historioco</p>
          	  </TabPanel>
          	</TabsBody>
          </div>
        </Tabs>     
      </div>   
    </div>
  );
}

export default NovedadesTabs;
